#crm-container{
    width: 100%;
    background-color: #ffff;
    padding-left: 16px;
    padding-right: 16px;
    .crm-header{
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        margin-botton: 5px;
        .crm-tabs{
            display: flex;
            .crm-option{
                background-color: #007be2;
                padding: 5px;
                border-radius: 5px;
                color: #fff;
                font-size: 12px;
                font-weight: 600;
                margin-right: 8px;
                i{
                    margin-right: 5px;
                }
            }
            .crm-option-inactive{
                background-color: #ccc;
                padding: 5px;
                border-radius: 5px;
                color: #000;
                font-size: 12px;
                font-weight: 600;
                margin-right: 8px;
                i{
                    margin-right: 5px;
                }
            }

        }
        .crm-right-header{
            display: flex;
            align-items: center;
            .crm-search-bar{
                border: 1px solid transparent;
                border-radius: 5px;
                overflow: hidden;
                display: flex;
                margin-right: 10px;
                input{
                    background-color: #ccc;
                    border: none;
                    width: 150px;
                    padding-left: 5px;
                    // margin-right: 5px;
                }
                input::placeholder{
                    font-size: 12px;
                }
                .crm-icon-search{
                    display: flex;
                    width: 30px;
                    justify-content: center;
                    align-items: center;
                    background-color: #007be2;

                }
            }
            .crm-bell{
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                margin-right: 10px;
                .num-noti{
                    position: absolute;
                    font-size: 8px;
                    background-color: red;
                    color: #fff;
                    border: 1px solid transparent;
                    border-radius: 100px;
                    width: 10px;
                    height: 10px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    top: -5px;
                    right: -3px;
                }
            }
            .crm-avatar{
                z-index: 1;
                .avatar{
                    width: 25px;
                    height: 25px;
                    border-radius: 100px;
                }
            }
        }
    }
}
.update-self{
    margin-right: 10px;
    .input-container{
        margin-bottom: 15px;
        .input-title{
            font-size: 12px;
            color: #787474;
        }
        input{
            border-radius: 5px;
            width: 150px;
            font-size: 12px;
            color: #787474;
            border: 0.2px solid #a9a4a4;
        }
        input::placeholder{
            color: #787474;
            font-size: 12px;
        }
    }
}
.list-staff-manager::-webkit-scrollbar {
    display: none;
  }
.profile-list-name::-webkit-scrollbar {
    display: none;
  }