
#root{
    width: 100%;
}
#layout{
    font-family: "Montserrat", sans-serif;
    width: 100%;
    overflow: hidden;
    .header{
        width: 100%;
        background: linear-gradient(to right,#007be2,#007be2);
        min-height: 35px;
        align-items: center !important;
        display: block;
    }
    .header_layout{
        display: flex;
        align-items: center;
        justify-content: space-between;
        // padding: 0px 15px;
        max-width: 1230px;
        margin: 0px auto;
        min-height: 35px;
        align-items: center !important;
        }
    .header-left{
        display: flex;
        flex-direction: row;
    }
    .header-right{
        display: flex;
        flex-direction: row;
    }
    @media (max-width: 768px) {
        .header-right{
            display: none;
        }
        .header_layout{
            justify-content: center !important;
            align-items: center !important;
        }
        .name-hide-for-small{
            display: none;
        }
        .contact-small{
            margin-top: 20px;
        }
        .address-small{
            margin-top: 20px;
        }
        .footer3{
            font-size: 12px !important;
        }
    }
    .contact-info{
        padding: 0px 5px;
        font-size: 12.5px;
        color: #fff;
    }
    .social-info{
        padding: 0px 5px;
        font-size: 12.5px;
        color: #fff;
    }
    .footer{
        background: linear-gradient(to right, rgba(0,137,254,1) 0%, rgba(0,62,164,1) 100%);
        overflow: hidden;
        padding-top: 30px;
        margin-top: 30px;
        padding-left: 15px;
        padding-right: 15px;
        color: #f1f1f1;
        .circles{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow: hidden;
            margin: 0px;
            padding: 0px;
            ul {
                list-style: disc;
            }
        }
        .circles li:nth-child(1) {
            left: 25%;
            width: 80px;
            height: 80px;
            animation-delay: 0s;
        }
        .circles li {
            position: absolute;
            display: block;
            list-style: none;
            animation: animate 25s linear infinite;
            bottom: -150px;
            border-radius: 10%;
            opacity: 0.2;
        }
        li {
            margin-bottom: 0.6em;
            text-align: -webkit-match-parent;
            img{
                transition: opacity 1s;
                opacity: 1;
                max-width: 100%;
                height: auto;
                display: inline-block;
                vertical-align: middle;
                border-style: none;
                overflow-clip-margin: content-box;
                overflow: clip;
            }
        }
        circles li img {
            -webkit-user-drag: none;
        }
        .widget-title{
            font-size: 1em;
            font-weight: 600 !important;
        }
        .is-divider{
            background-color: rgba(255,255,255,.3);
            height: 3px;
            max-width: 30px;
            display: block;
            width: 100%;
            margin-top: 10px;
            margin-bottom: 10px;
        }
        .widget-text{
            margin-top: 2px;
            margin-bottom: 2px;
            font-size: 13px;
        }
        .row{
            padding-top: 30px;
            padding-bottom: 30px;
            border-bottom: 1px solid #f1f1f1;
            max-width: 1230px;
            margin: auto
        }
        .footer3{
            padding-top: 10px;
            padding-bottom: 10px;
            font-size: 0.9em;
            max-width: 1230px;
            margin: auto
        }
    }
}