.loading {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 999;
  }
  
  .loading-spinner {
    width: 40px;
    height: 40px;
    border: 3px solid #fff;
    border-top-color: #007bff;
    border-radius: 50%;
    animation: spin 0.8s infinite linear;
  }
  
  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }