.img-1,
.img-2 {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  animation-duration: 40s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
  img{
    max-width: 100%;
    height: auto;
  }
}

  .img-1 {
    animation-name: fade-1;
    animation-delay: 0s;
    z-index: 1;
  }

  .img-2 {
    animation-name: fade-2;
    animation-delay: 8s;
    z-index: 2;
  }

  @keyframes fade-1 {
    0% {
      opacity: 0;
      z-index: 1;
    }
    25% {
      opacity: 1;
      z-index: 1;
    }
    50% {
      opacity: 1;
      z-index: 2;
    }
    75% {
      opacity: 0;
      z-index: 2;
    }
    100% {
      opacity: 0;
      z-index: 1;
    }
  }

  @keyframes fade-2 {
    0% {
      opacity: 0;
      z-index: 2;
    }
    25% {
      opacity: 0;
      z-index: 1;
    }
    50% {
      opacity: 1;
      z-index: 1;
    }
    75% {
      opacity: 1;
      z-index: 2;
    }
    100% {
      opacity: 0;
      z-index: 2;
    }
  }
