  width: 100%;
  height: 700px;
  position: relative;
  align-items: center;
}

.slider section {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.slider section.active {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}

.slide-animation {
  animation: slide 20s 1;
  animation-delay: 20s;
}

.title_trademark h1.activex {
