.react-pageflip {
  width: 100%;
  height: 100%;
  position: relative;
}

.react-pageflip__page-wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  transform-origin: 0 0;
}

.react-pageflip__page {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  backface-visibility: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}

.react-pageflip__page--right {
  transform: rotateY(180deg);
  z-index: 1;
}

.react-pageflip__page--left {
  z-index: 2;
}

.react-pageflip__page--flipped {
  z-index: 3;
}

