.container-image-profile{
  position: relative;
  cursor: pointer;
  z-index: 1;
}

.image-profile{
  height: 340px;
  @include transition(all, 0.3s, ease-in-out);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.container-image-profile:hover .image-profile{
  transform: scale(1.1);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.container-image-profile:hover .mask-image-profile {
  transform: scale(1.1);
  background-color: red($color: #000000);
  opacity: 1;
}

.container-image-profile:hover .vien-sach-profile {
  transform: scale(1.1);
  right: -23px !important;
  width: 10px;
}

.mask-image-profile {
  display: block;
  width: 100%;
  border-top: 3px white solid ;
  border-bottom: 3px white solid ;
  height: 50px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Change the color and opacity to create the desired mask effect */
  position: absolute;
  top: 30%;
  left: 0;
  opacity: 1;
  @include transition(all, 0.3s, ease-in-out);
}

.tittle-mask-profile-image{
  margin: 10px 0;
  text-align: center;
  color: white;
}

.wapper-list-image{
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  background-color: #F6F6F6;
}

.container-news-slider{
  width: 1290px !important;
  margin: 0 auto;
}

.box-news-single{
  max-width: 97vw;
  background-color: #fafafa;
  border-radius: 10px;
  box-shadow: 3px 3px 3px #EAEAEA;
  margin-bottom: 50px;
  margin-right: 20px;
}

.news-image-container-single{
  width: 100%;
  height: auto;
  border-bottom: 5px #007BE2 solid;
}

.image-news-single{
  object-fit: contain;
  max-width: 97vw;
  margin: 0 auto;
  border-radius: 10px 10px 0 0;
}

.image-shelf-profile{
  background-image: url('../../../public/assets/images/wall-bookshelf.png');
  background-repeat: no-repeat;
  background-size: 90% 120px;
  background-position: bottom;
  width: 100%;
  height: 120px;
  position: absolute;
  left: 0;
  padding: 20px auto;
  margin-top: 310px;
  display: block;
  content: "";
  background-color: #f6f6f6;
}

.wapper-image-profile{
  display: flex;
}

.vien-sach-profile{
  background-color: transparent;
  width: 9px;
  height: 100%;
  position: absolute;
  @include transition(all, 0.3s, ease-in-out);
  right: -9px;
}

.box-profile{
  display: flex;
  position: relative;
}

.ChatBox {
  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 999;
  .ImageChatBox {
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
  }
  .TextChatBox {
    width: 200px;
    height: 35px;
    background: #007BE2;
    border-radius: 15px 15px 0px 0px;
    padding: 2px 15px;
    font-size: 18px;
    color: #fff;
  }
  .openChat {
    display: block;
  }
  .closeChat {
    display: none;
  }
  .screen-chat {
    width: 350px;
    height: 450px;
    background: #fff;
    border-radius: 15px;
    position: relative;
    overflow: hidden;
    //shadow right-hand
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    //animation
    animation: openChat 0.5s ease-in-out;
    animation-fill-mode: forwards;
  }
  .chat-header {
    width: 100%;
    height: 50px;
    background: #007BE2;
    border-radius: 15px 15px 0px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 15px;
  }
  .chat-avt {
    display: flex;
    align-items: center;
  }
  .chat-img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .chat-img img {
    width: 90%;
    height: 90%;
    border-radius: 50%;
  }
  .chat-title {
    margin-left: 10px;
  }
  .chat-title .chat-name {
    font-size: 16px;
    color: #fff;
    font-weight: 500;
  }
  .chat-title .chat-status {
    font-size: 12px;
    color: #ececec;
  }
  .chat-close {
    background-color: transparent;
    background-repeat: no-repeat;
    border: none;
    cursor: pointer;
    overflow: hidden;
    outline: none;
    font-size: 30px;
    color: #fff;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .chat-close:hover {
    background-color: #c9ddef;

  }
  .chat-message {
    display:block;
    overflow-x: hidden;
    overflow-y: scroll;
    position:relative;
    width:100%;
    height: 80%;
    padding:2% 3%;
  }
  .chat-message::-webkit-scrollbar {
    width: 5px;
  }
  .chat-input {
    width: 100%;
    height: 50px;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: black 1px solid;
    position: absolute;
    bottom: 0;
  }
  .input-chat input{
    width: 80%;
    height: 100%;
    border: none;
    outline: none;
    padding: 0 10px;
    font-size: 13px;
  }
  .icon-chat {
    width: 20%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .iconLike {
    cursor: pointer;
  }
  .iconLikeOrDislike .Like {
    color: red;
  }
  .iconLikeOrDislike .Like:hover {
    font-size: 20px;
  }
  .iconLikeOrDislike .Dislike {
    color: green;
  }
  .iconLikeOrDislike .Dislike:hover {
    font-size: 20px;
  }
  .chat-user-sent{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin: 10px 0;
  }
  .chat-user-sent .chat-bubble-adative {
    display: flex;
    width: 100%;
    flex-direction: row-reverse;
  }
  .chat-bot-received .chat-bubble-adative{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin: 10px 0;
  }
  .chat-user-sent .chat-bubble-text {
    width: 70%;
    background: #007BE2;
    border-radius: 15px;
    padding: 10px 15px;
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
  }
  .chat-bot-received .chat-bubble-text {
    width: 70%;
    background: #fff;
    border-radius: 15px;
    padding: 10px 15px;
    color: #000;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    //shadow
    box-shadow: 0 0 1px rgba(59, 59, 59, 0.5);
  }
  .chat-bubble-date {
    font-size: 12px;
    color: #000;
    margin: 5px 0;
  }
}


