#ContractFull
{
    font-size: 12px;
    // background-color: #a8a8a8;
    padding: 5px 300px 5px 300px;
    .ContractContainer {
        // border: 1px solid #4d4d4d;
        border-radius: 15px;
        background-color: #ececec;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }
    // .checkBoxCus {
    //     display: none; /* Hide the default checkbox */
    //   }\
    .text-header {
        font-size: 20px;
        font-weight: bold;
        color: #747474;
    }
      .checkBoxCus {
        width: 18px;
        height: 18px;
        background-color: #fff;
        border: 2px solid #999;
        border-radius: 4px;
        cursor: pointer;
      }
     
    .form-control {
        height: 40px;
        font-size: 14px;
    }
    .form-control:focus {
        box-shadow: none !important;
    }
    .checkbox-cus {
        width: 17px;
        height: 17px;
    }
    .form-select {
        // height: 30px;
        font-size: 14px;
        background-color: #ececec;
        border: 1px solid #ced4da;
    }
    .form-select option {
        background-color: white;
        border: 1px solid #ced4da;
    }
    .form-select option:hover {
        background-color: rgb(135, 207, 252);
        border: 1px solid #5bb5cc;
    }
    .form-select:hover {
        border-color: #5bb5cc;
        background-color: rgb(173, 220, 250);
        transition: 1s;
    }
    .form-select:focus {
        box-shadow: none !important;
    }
    .btn-cus {
       width: 100%;
       height: 35px;
       border: 1px solid #ced4da;
    }
    .btn-cus-white {
        background-color: #fff;
        color: #000;
    }
    .btn-cus-gray {
        background-color: #ececec;
        color: #000;
    }
    .btn-cus:hover {
        background-color: #95bdfa;
        border: 1px solid #333333;
        transition: 0.3s;
    }
    .form-text-area {
        width: 100%;
        border: 1px solid #ced4da;
        padding: .375rem .75rem;
        font-weight: 400;
        line-height: 1.5;
        color: #212529;
        background-color: #fff;
        background-clip: padding-box;
        border-radius: .25rem;
    }
    .form-text-area:focus {
        box-shadow: none !important;
        color: #212529;
        background-color: #fff;
        border-color: #86b7fe;
        outline: 0;
    }
    .Header {
        // background-color: #ececec;
        padding: 0px 10px 15px 10px;
    }
    .Body {
        // background-color: #f3f3f3f3;
        opacity: 0.8;
        padding: 15px 10px 15px 10px;
        height: 100%;

        .contract-tabs {
            display: flex;
            align-items: center;
            .contract-option {
                text-align: center;
                margin: 0px 10px;
                padding-bottom: 5px;
                border-bottom: 4px solid #0896dc;;
                color: black;
            }
            .contract-option:hover {
                cursor: pointer;
                // color: #4d4d4d;
            }
            .contract-option-inactive {
                margin: 0px 10px;
                padding-bottom: 10px;
                transition: cubic-bezier(0.075, 0.82, 0.165, 1);
            }
            .contract-option-inactive:hover {
                cursor: pointer;
                color: #4d4d4d;
            }
        }

        .contract-content {
            padding: 5px 10px 10px 10px;
            border: 1px solid #ced4da;
            background-color: #fff;
            height: 445px;
            .body_contract {
                border: 1px solid #ced4da;
                border-radius: 5px;
                /* Just common table stuff. Really. */
                table  { border-collapse: collapse;  }
                table tr td{
                    width: 100px;
                }
                table tr td:first-child{
                    width: 360px;
                }
                .table-header { 
                    position: sticky; 
                    top: 0; 
                    z-index: 1; 
                    background-color: #ececec;
                    border-bottom: #ced4da;
                    table tr td{
                        border-right: 1px solid #ced4da;
                        text-align: center;
                    }
                    table tr td:hover {
                        cursor: pointer;
                        background-color: #86b7fe;
                    }
                }
                .table-body {
                    overflow: auto;
                    height: 290px; 
                    padding-top: 5px;
                    table tr:hover {
                        background-color: #86b7fe;
                    }
                    .form-input-table {
                        width: 90px;
                        border: none;
                        background-color: transparent;
                        text-align: center;
                        &:focus {
                            outline: none;
                        }
                    }
                    .deleteIcon {
                        cursor: pointer;
                    }
                    .deleteIcon:hover {
                        color: #f85c5c;
                        transition: 0.2s;
                    }
                }
                
            }
    
            
        }

        .footer_contract {
            padding-top: 10px;
        }
    }
    


    
}