body {
  font-family: sans-serif;
}

.react-pdf__Document {
  display: flex;
  overflow: hidden;
}
.flip-container {
  max-width: 1230px;
  margin: auto auto;
  opacity: 1;
}
.button-prev {
  z-index: 9999999;
  width: 35px;
  height: 35px;
  background-size: 80%;
  background-color: #9a9a9a7d;
  opacity: 0.7;
  cursor: pointer;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}
.contain {
  display: "flex";
  justify-content: "center";
  align-items: "center";
}
@media (max-width: 768px) {
  .button-prev {
    z-index: 9999999;
    width: 35px;
    height: 35px;
    background-size: 80%;
    background-color: #9a9a9a7d;
    opacity: 0.7;
    cursor: pointer;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    left: 35%;
    bottom: 0px;
  }
  .button-next {
    z-index: 9999999;
    width: 35px;
    height: 35px;
    background-size: 80%;
    background-color: #9a9a9a7d;
    opacity: 0.7;
    cursor: pointer;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    bottom: 0px;
    position: absolute;
    right: 35%;
  }
  .contain {
    display: "flex";
    justify-content: "center" !important;
    align-items: "center" !important;
    position: relative;
    overflow: hidden;
  }
  .react-pdf__Page {
    width: 300px;
    height: 423px;
  }
  .stf__item.--soft.--right.--simple {
    border-left: 0px solid #ccc;
    box-shadow: -5px 0px 55px rgba(0, 0, 0, 0.5), 4px 4px 0 0 rgba(0, 0, 0, 0.1),
      6px 6px 0 0 rgba(0, 0, 0, 0.1);
  }
  .container-image-flip-book{
    margin-top: 86px !important;
  }
}
@media (min-width: 768px) {
  .button-prev {
    z-index: 9999999;
    width: 35px;
    height: 35px;
    background-size: 80%;
    background-color: #9a9a9a7d;
    opacity: 0.7;
    cursor: pointer;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    top: 50%;
    left: -40px;
  }
  .button-next {
    z-index: 9999999;
    width: 35px;
    height: 35px;
    background-size: 80%;
    background-color: #9a9a9a7d;
    opacity: 0.7;
    cursor: pointer;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    bottom: 50%;
    right: -40px;
    position: absolute;
  }
  .stf__parent{
    margin: 5vh auto ;
  }
  .contain {
    display: "flex";
    justify-content: "center" !important;
    align-items: "center" !important;
    position: relative;
    /* overflow: hidden; */
    max-height: 858px;
  }
  .react-pdf__Page {
    max-width: 600px;
    height: 858px;
  }
}
.stf__item {
  overflow: hidden;
}
.canvas {
  overflow: hidden;
}
.button-next {
  z-index: 9999999;
  width: 35px;
  height: 35px;
  background-size: 80%;
  background-color: #9a9a9a7d;
  opacity: 0.7;
  cursor: pointer;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}
.react-pageflip {
  margin: 0 auto;
}
.react-pdf__Document {
  align-items: center;
  justify-content: center;
}
.stf__item.--soft.--right.--simple {
  border-left: 1px solid #ccc;
  box-shadow: -5px 0px 55px rgba(0, 0, 0, 0.5), 4px 4px 0 0 rgba(0, 0, 0, 0.1),
    6px 6px 0 0 rgba(0, 0, 0, 0.1);
}

.container-pdf-reader {
  position: relative;
  margin-top: 40px;
}


:focus-visible {
  outline: none;
}

/* .image-flip-book {
  width: calc(100% * 0.4);
  height: calc(100% * 0.6);
} */


#modal-flip-book{
  .container-image-flip{
    margin-top: -25px;
  }
  
  .icon-cancel {
    position: fixed;
    top: 50px;
    right: 50px;
    transform: translate(-50%, -50%);
  }
  
}