#profile-container{
  .row-profile:hover {
    background-color: #e8efff !important;
  }
}
.modal-add-profile{
    .loading-container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        position: absolute;
        left: 47%;
      }
      
      .loading-spinner {
        border-radius: 50%;
        width: 40px;
        height: 40px;
        border-top: 0.5rem solid rgba(0, 0, 0, 0.2);
        border-right: 0.5rem solid rgba(0, 0, 0, 0.2);
        border-bottom: 0.5rem solid rgba(0, 0, 0, 0.2);
        border-left: 0.5rem solid #000000;
        animation: spin 1s linear infinite;
      }
      
      @keyframes spin {
        to {
          transform: rotate(360deg);
        }
      }
    .input-container{
        margin-bottom: 15px;
        .input-title{
            font-size: 12px;
            color: #787474;
        }
        input{
            border-radius: 5px;
            width: 500px !important;
            font-size: 12px;
            color: #787474;
            border: 0.2px solid #a9a4a4;
        }
        input::placeholder{
            color: #787474;
            font-size: 12px;
        }
    }
}