#staff{
    .main-container{
        margin-top: 5px;
        width: 100%;
        height: calc(100vh - 43px);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .content-bar{
            // height: calc(100vh - 43px);
            display: flex;
            background-color: #007be2;
            font-size: 10px;
            font-weight: 600;
            color: #fff;
            border-left: 1px solid #ccc;
            border-right: 1px solid #ccc;
            .table-line{
                border-bottom: 1px solid #ccc;
                width: 1px;
                background-color: #ccc;
            }
            .table-stt{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 2%;
            }
            .table-name{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 20%;
            }
            .table-phone{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 10%;
            }
            .table-mail{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 15%;
            }
            .table-username{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 10%;
            }
            .table-password{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 8%;
            }
            .table-role{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 9%;
            }
            .table-accnumber{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 15%;
            }
            .table-update{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                flex: 1
            }
            .table-bank{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                flex: 4%
            }
        }
    }
    .table-data:hover {
        background-color: #e8efff !important;
    }
    .table-data{
        display: flex;
        font-size: 14px;
        font-weight: 400;
        text-align: left !important;
        color: #000;
        border-left: 1px solid #ccc;
        border-right: 1px solid #ccc;
        .table-line{
                border-bottom: 1px solid #ccc;
                width: 1px;
                background-color: #ccc;
            }
            .table-stt{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 2%;
                overflow: hidden;
            }
            .table-name{
                border-bottom: 1px solid #ccc;
                display: flex;
                padding-left: 5px;
                width: 20%;
                overflow: hidden;
            }
            .table-phone{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 10%;
                overflow: hidden;
            }
            .table-mail{
                border-bottom: 1px solid #ccc;
                display: flex;
                padding-left: 5px;
                width: 15%;
                overflow: hidden;
            }
            .table-username{
                border-bottom: 1px solid #ccc;
                display: flex;
                padding-left: 5px;
                width: 10%;
                overflow: hidden;
            }
            .table-password{
                border-bottom: 1px solid #ccc;
                display: flex;
                padding-left: 5px;
                width: 8%;
                overflow: hidden;
            }
            .table-role{
                border-bottom: 1px solid #ccc;
                display: flex;
                padding-left: 5px;
                width: 9%;
                overflow: hidden;
            }
            .table-accnumber{
                border-bottom: 1px solid #ccc;
                display: flex;
                padding-left: 5px;
                width: 15%;
                overflow: hidden;
            }
            .table-bank{
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: center;
                align-items: center;
                flex: 4%;
                overflow: hidden;
            }
            .table-update{
                border-bottom: 1px solid #ccc;
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                flex: 1;
                overflow: hidden;
                i:first-child {
                    // margin-right: 10px;
                }
            }
    }
    .right-mount-option:hover {
        background-color: #e8efff !important;
    }
    .right-mount-option{
        border-bottom: 1px solid #ccc;
        padding-left: 10px;
        padding-right: 10px;
        font-size: 12px;
        padding-top: 5px;
        padding-bottom: 5px;
        i{
            margin-right: 5px;
        }
    }
    ::-webkit-scrollbar {
        display: none;
      }
    .list-data{
        height: calc(100vh - 150px);
        overflow: scroll;
    }
}
.popup-content{
    display: flex;
    background-color: #fff;
    border-radius: 14px;
    position: relative;
    .icon-cancel{
        position: absolute; 
        top: 5px; 
        right: 10px;
        i:hover{
            color: #007be2;
        }
    }
    .right-popup{
        padding: 10px;
        display: flex;
        .right-left-con{
            margin-right: 10px;
            .content-detail{
                display: flex;
            }
        }
        .header-right-left{
            font-size: 22px;
            font-weight: 500;
        }
        .input-container{
            margin-bottom: 15px;
            .input-title{
                font-size: 12px;
                color: #787474;
            }
            input{
                border-radius: 5px;
                width: 150px;
                font-size: 12px;
                color: #787474;
                border: 0.2px solid #a9a4a4;
            }
            input::placeholder{
                color: #787474;
                font-size: 12px;
            }
        }
        .button-add-container{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
            .button-add{
                height: 35px;
                width: 170px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #fff;
                font-size: 15px;
                font-weight: 600;
                border-radius: 5px;
                background-color: #007be2;
            }
        }
    }
    // .line-popup{
    //     width: 1px;
    //     background-color: #ccc;
    //     margin-top: 10px;
    //     margin-bottom: 10px;
    // }
    .left-popup{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 10px;
        img{
            width: 120px;
            height: 120px;
        }
    }
}

