.popup-content{
    display: flex;
    background-color: #fff;
    border-radius: 14px;
    position: relative;
    .icon-cancel{
        position: absolute; 
        top: 5px; 
        right: 10px;
        i:hover{
            color: #007be2;
        }
    }
    .right-popup{
        padding: 10px;
        display: flex;
        .right-left-con{
            margin-right: 10px;
            .content-detail{
                display: flex;
            }
        }
        .header-right-left{
            font-size: 22px;
            font-weight: 500;
        }
        .input-container{
            margin-bottom: 15px;
            .input-title{
                font-size: 12px;
                color: #787474;
            }
            input{
                border-radius: 5px;
                width: 150px;
                font-size: 12px;
                color: #787474;
                border: 0.2px solid #a9a4a4;
            }
            input::placeholder{
                color: #787474;
                font-size: 12px;
            }
        }
        .button-add-container{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 10px;
            .button-add{
                height: 35px;
                width: 170px;
                display: flex;
                justify-content: center;
                align-items: center;
                color: #fff;
                font-size: 15px;
                font-weight: 600;
                border-radius: 5px;
                background-color: #007be2;
            }
        }
    }
    .line-popup{
        width: 1px;
        background-color: #ccc;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .left-popup{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 10px;
        img{
            width: 120px;
            height: 120px;
        }
    }
}

