#login{
    width: 100vw;
    height: 100vh;
    background-image: url("../../../public/assets/images/background_login.png");
    .logo-container{
        width: 320px;
        margin: auto;
        padding: 5% 0px 0px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        img{
            width: 280px;
            height: 80px;
            margin-left: 25px;
        }
        .info-container{
            margin-top: 20px;
            padding: 26px 24px 34px 24px !important;
            border: 1px solid;
            border-radius: 5px;
            background-color: rgba(255,255,255,0.6);
            a{
                text-decoration: none;
            }
        }
        .info{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            input{
                width: 100%;
            }
        }
        .info-pass{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .input-pass{
                width: 100%;
                display: flex;
                align-items: center;
                border: 1px solid;
                input{
                    width: 90%;
                    outline: none;
                    border: none;
                }
                i{
                    margin-left: 5px;
                }
            }
        }
        .title-input{
            font-size: 14px;
            font-weight: 400;
            margin-bottom: 3px !important;
            // margin-left: 22px;
        }
        .title-input-pass{
            font-size: 14px;
            font-weight: 400;
            margin-bottom: 3px !important;
            // margin-left: 22px;
            margin-top: 16px;
        }
    }
    .auto-login-bar{
        // margin: auto;
        padding: 5% 0px 0px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        font-size: 14px;
        font-weight: 400;
        .login-button{
            padding: 5px 10px;
            background-color: #EFA024;
            color: #fff;
            font-size: 13px;
        }
    }
    .auto-login{
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 8px;
        div{
            margin-left: 5px;
        }
    }
    .info-container2{
        padding: 26px 24px 34px 24px !important;
        font-size: 13px;
        color: #50575e;
        a{
            text-decoration: none;
            color: #50575e;
            font-size: 13;
        }
        a:hover{
            color: #2271b1;
        }
        .back-to-home{
            margin-top: 12px;
            display: flex;
            justify-content: center;
            align-items: center;
            a{
                text-align: center;
            }
        }
    }
    .info-container3{
        padding: 10px !important;
        margin-top: 34px;
        margin-bottom: 34px;
        font-size: 13px;
        color: #50575e;
        border-left: 3px solid #2271b1;
        .message{
            text-align: left;
        }
        p{
            margin-bottom: 0px !important;
        }
    }
    .info-container4{
        padding: 10px !important;
        margin-top: 34px;
        margin-bottom: 34px;
        font-size: 13px;
        color: #50575e;
        border-left: 3px solid #d63638;
        .message{
            text-align: left;
        }
        p{
            margin-bottom: 0px !important;
        }
    }

}
