@import url("https://fonts.googleapis.com/css?family=Montserrat");

body {
  font-family: "Montserrat", sans-serif;
}

@mixin transition($properties, $duration, $timing) {
    -webkit-transition: $properties $duration $timing;
}
#menu{
    max-width: 1230px;
    margin: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    .menu-logo{
        margin-right: 60px;
        width: 250x;
        height: 70px;
    }
    .menu-left{
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    .bar-option{
        display: flex;
        flex-direction: row;
    }
    .option-menu{
        padding: 10px;
        color: #262f5a;
        font-size: 15px;
        font-weight: 700;
    }
    .option-menu-blog{
        padding: 10px;
        color: #262f5a;
        font-size: 15px;
        font-weight: 700;
        .sub-menu{
            display: none;
        }
    }
    .option-menu:hover{
        color: #007be2;
    }
    .option-menu-blog:hover{
        color: #007be2;
        position: relative;
        .sub-menu{
            position: absolute;
            z-index: 99;
            display: flex;
            min-width: 260px;
            // max-height: 0px;
            display: flex;
            flex-direction: column;
            color: #262f5a;
            text-align: left;
            background-color: #fff;
            left: -5px;
            border-radius: 5px;
            padding: 30px;
            box-shadow: 1px 1px 15px rgba(0,0,0,.15);
            .sub-menu-text{
                margin: 5px 0px;
                border-bottom: 1px solid #d7d7d7;
            }
        }
    }
    .menu-right{
        background-color: #007be2;
        border-radius: 4px;
        min-height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        @include transition(all, 0.3s, ease-in-out);
    }
    .menu-right:hover{
        background-color: #fff;
        border: 1px solid #007be2;
        @include transition(all, 0.3s, ease-in-out);
        .contact{
            color: #007be2;
        }

    }
    .contact{
        margin: 4px 20px;
        font-size: 14px;
        font-weight: 700;
        color: #fff;
    }
}
