.drawer-right-container{
    padding: 10px 16px;
    img{
        width: 150px;
        height: auto;
    }
}
.drawer-right-logo{
    display: flex;
    i{
        margin-left: 30px;
        animation: slide-up 1s forwards;
        @keyframes slide-up {
            from {
              opacity: 0;
              transform: translateY(100%);
            }
            to {
              opacity: 1;
              transform: translateY(0);
            }
          }
    }
}
.drawer-option{
    .option{
        color: #222;
        font-size: 14px;
        padding-top: 15px;
        padding-left: 15px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        border-bottom: 1px solid #ececec ;
        font-weight: 700;
        i{
            font-size: 15px;
        }
    }
}
.modal-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgba(0,0,0,.6);
    justify-content: center;
    align-items: center;
}
.icon-x{
    background-color: rgba(0,0,0,.6);
    text-align: right;
    padding-right: 20px;
    padding-top: 20px;
    i{
        color: #fff;
    }
}
.modal-search{
    width: 100%;
    height: 100%;
    i{
        align-self: flex-end;
    }
}
.search-bar{
    display: flex;
    background-color: rgba(255,255,255,.2) !important;
    padding: 12px;
    align-items: center;
    border: 1px solid rgba(255,255,255,.09);
    border-radius: 5px;
}
.search-bar i {
    margin-bottom: 2.5px;
    margin-left: 10px;
    color: #fff;
  }
  .search-bar input {
    background-color: rgba(255,255,255,.2) !important;
    border: none;
    color: #fff;
  }
  .search-bar input::placeholder{
    color: #fff;
    font-size: 20px;
  }
  .search-bar input:focus{
    outline: none;
    box-shadow: none;
  }
@media (max-width: 768px) {
    #menu{
        display: none;
    }    
}
@media (min-width: 768px) {
    #menu-resp{
        display: none;
    }
}